@import "./utils/vars";
@import "./utils/mixins";

.footer {
  height: 540px;
  width: 100%;
  background: url(../images/photos/footer.png) no-repeat center;
  background-size: cover;
  position: relative;

  &__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
